import { PropsWithChildren } from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Error, WarningAmber } from "@rp/react/web/mui/icons"

function WatchlistDataTableErrorCell({
  error,
  children,
}: PropsWithChildren<{ error?: Error | null }>) {
  if (error) {
    return (
      <RPTooltip title={error.message} placement="top">
        <WarningAmber sx={{ color: "error.main", fontSize: 14 }} />
      </RPTooltip>
    )
  }

  return children
}

export { WatchlistDataTableErrorCell }
